import {
	request
} from './request'

//获取表单所有组件字段、名字的信息
export function gettableColumn(params) {
	return request({
		url: '/FormController/getTemplates/' + params,
		// params: params
	})
}
//获取一个表单的信息
export function getOneTable(params) {
	return request({
		url: '/FormController/form/' + params,
		// params: params
	})
}

//获取表单的所有用户填写的数据
export function gettableData(params) {
	return request({
		url: '/DataController/getAllData/' + params,
	})
}


//删除单项表单数据
export function deletetableData(params) {
	return request({
		method: 'delete',
		url: '/DataController/deleteBatch?id=' + params,
	})
}

//查看当天填写人数
export function getDataCountDaily(params) {
	return request({
		url: '/DataController/getDataCountDaily/' + params,
	})
}
