<template>
  <div class="fd-all">
    <!-- <el-button :plain="true" @click="gettableDatas">警告</el-button> -->
    <el-dialog
      title="添加数据"
      :visible.sync="dialogVisible"
      width="40%"
      class="form-table"
    >
      <!-- 填写表单 -->
      <component
        v-for="(item, index) in form.templateList"
        :is="iscomponent(item.templateName)"
        :data="item"
        :index="index + 1"
        :key="index"
      ></component>
      <!-- 尾部的按钮 -->
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="commit">确 定</el-button>
      </span>
    </el-dialog>
    <div class="fd-head">
      <tbody>
        <tr class="fd-tr">
          <td>
            <el-button
              class="iconfont gd-shuaxin"
              type="text"
              @click="refreshData"
              >刷新页面</el-button
            >
          </td>
          <td>
            <el-button
              class="iconfont gd-tianjia"
              type="text"
              @click="dialogVisible = true"
              >增加数据</el-button
            >
          </td>
          <td>
            <el-button class="iconfont gd-shanchu" type="text" @click="delData"
              >删除数据</el-button
            >
          </td>
        </tr>
      </tbody>
    </div>
    <div class="fd-main">
      <!-- 总表max-height="300" -->
      <el-table
        :data="tableData"
        stripe
        @select-all="getSelectAll"
        @select="getSelect"
        @row-click="getDetails"
      >
        <!-- 左侧多选 -->
        <el-table-column type="selection" width="55"></el-table-column>
        <!-- 序列 -->
        <el-table-column type="index" width="55"></el-table-column>
        <!-- 表头 使用axios读取本地json数据获取组件 -->
        <el-table-column
          v-for="(item, index) in tableColumn"
          :key="index"
          :prop="item.prop"
          :label="item.label"
          style="width: '150'"
          sortable
        ></el-table-column>
      </el-table>
    </div>
  </div>
</template>

<script>
import {
  gettableColumn,
  gettableData,
  deletetableData
} from "@/network/formdata.js";
import t_checkbox from "@/components/EditPageComp/tablecomp/CheckBox.vue";
import t_radio from "@/components/EditPageComp/tablecomp/Radio.vue";
import t_select from "@/components/EditPageComp/tablecomp/Select.vue";
import t_textbox from "@/components/EditPageComp/tablecomp/TextBox.vue";
import t_picradio from "@/components/EditPageComp/tablecomp/PicRadio.vue";
import comp from "@/utils/comp.js";
import { getTableData, submitTableData } from "@/network/tabledata.js";
// import FillTable from "@/components/EditPageComp/BodyComponent/FillTable.vue";
export default {
  components: {
    "t-checkbox": t_checkbox,
    "t-radio": t_radio,
    "t-select": t_select,
    "t-textbox": t_textbox,
    "t-picradio": t_picradio
  },
  inject: ["reload"],
  data() {
    return {
      dialogVisible: false,
      tableColumn: [],
      tableData: [],
      multipleSelection: [],
      selectData: [],
      formId: "",
      form: []
    };
  },
  methods: {
    // 单选获取信息
    getSelect(row) {
      this.selectData = [];
      for (var i = 0; i < row.length; i++) {
        //选中的行，将行里面的dataId存入数组
        this.selectData.push(row[i].dataId);
      }
    },
    // 全选获取信息
    getSelectAll(selection) {
      this.getSelect(selection);
    },

    //点击获取单行的信息
    getDetails(row) {
      this.dialogVisible = true;
      console.log(row);
    },

    //刷新页面
    refreshData() {
      this.reload();
    },
    addData() {},

    //多选删除
    delData() {
      deletetableData(this.selectData).then(res => {
        console.log(res);
        if (res.code == 1) {
          this.reload();
        }
      });
    },

    gettableDatas() {
      this.axios
        .all([gettableData(this.formId), gettableColumn(this.formId)])
        .then(
          this.axios.spread((res1, res2) => {
            var allData = res1.data;
            var allQuestion = res2.data;
            var ans = [];
            var arrData = [];

            //将返回的标题头 id 与 标题文字 拼接成键值对，存入数组 prop和label，以符合element ui 的格式
            for (let questionIndex in allQuestion) {
              // console.log(res.data[questionIndex].templateContent);
              var paramOption = {};
              paramOption.prop = allQuestion[questionIndex].formTemplateId + "";
              paramOption.label = allQuestion[questionIndex].templateTitle;
              this.tableColumn.push(paramOption);
            }

            //答案与id拼接
            if (allData[0] === undefined) {
              this.$message({
                message: "暂无数据哟~(#^.^#)",
                type: "warning"
              });
            } else {
              for (let i in allData) {
                //循环表身单行 拿 表身的formTemplateId
                for (let j in allQuestion) {
                  //循环表头 拿 表头的formTemplateId，
                  let paramId = {};
                  let paramData = {};
                  paramId["dataId"] = allData[i].dataId;
                  // arrAns.push(paramData)
                  for (let k in allData[i].dataDetailsList) {
                    //判断表身formTemplateId和表头formTemplateId是否相等
                    if (
                      allQuestion[k].formTemplateId ==
                      allData[i].dataDetailsList[j].formTemplateId
                    ) {
                      //如果相等，就拿表头中的 问题类型templateName 开始判断
                      //----------------------------------1、如果为单选 则处理object
                      if (
                        allQuestion[k].templateName == "radio" ||
                        allQuestion[k].templateName == "select" ||
                        allQuestion[k].templateName == "picradio"
                      ) {
                        //如果不为空值或者空数组，做处理
                        if (
                          allData[i].dataDetailsList[j].dataContent != "[]" &&
                          allData[i].dataDetailsList[j].dataContent != ""
                        ) {
                          ans = JSON.parse(
                            allData[i].dataDetailsList[j].dataContent
                          );
                          ans = ans.value;
                        }
                        //空值则赋一个“”
                        else {
                          ans = JSON.parse('{"key":0,"value":""}');
                          ans = ans.value;
                        }
                        // console.log(ans);
                      }
                      //----------------------------------2、如果为多选 则处理array
                      else if (
                        allQuestion[k].templateName == "checkbox" ||
                        allQuestion[k].templateName == "piccheckbox"
                      ) {
                        //如果为不为空值或者空数组，做处理
                        if (
                          allData[i].dataDetailsList[j].dataContent != "[]" &&
                          allData[i].dataDetailsList[j].dataContent != ""
                        ) {
                          // 将数组拆开拿每一个值存入对应位置
                          let result = eval(
                            allData[i].dataDetailsList[j].dataContent
                          );
                          ans = result.map(option => option.value + " ");
                        }
                        //空值则赋一个“”
                        else {
                          ans = JSON.parse('{"key":0,"value":""}');
                          ans = ans.value;
                        }
                      }
                      //----------------------------------3、如果为填空，直接使用
                      else {
                        ans = allData[i].dataDetailsList[j].dataContent;
                      }
                    }
                  }
                  paramData[allData[i].dataDetailsList[j].formTemplateId] = ans;
                  // console.log(paramData);
                  arrData.push(paramId);
                  arrData.push(paramData);
                }
                let result = Object.assign(...arrData);
                arrData = [];
                this.tableData.push(result);
              }
            }
          })
        );
    },
    // 获取组件名
    iscomponent(component) {
      return comp.is_table_comp(component);
    },
    // 提交表单
    commit() {
      this.dialogVisible = false;
      if(comp.checkRequired(this.form)){
        submitTableData(comp.commitForm(this.form)).then(() => {
          this.reload();
        });
      }else{
        this.$message({
          message: "必填项不能为空(带*选项)!",
          type: "error",
        });
      }
    }
  },

  //钩子函数，页面创建时
  created() {
    sessionStorage.setItem("TagActiveName", "third");
    this.formId = this.$route.params.id;
    //请求表头表身数据
    this.gettableDatas();
    //获取表单
    getTableData(this.$route.params.id).then(res => {
      let index = 0;
      let arr = [];
      while (index < res.data.templateList.length) {
        for (let i in res.data.templateList) {
          if (res.data.templateList[i].templateCount === index) {
            arr.push(res.data.templateList[i]);
          }
        }
        index++;
      }
      res.data.templateList = arr;
      for (let r in res.data.templateList) {
        if (
          res.data.templateList[r].templateContent !== null &&
          res.data.templateList[r].templateContent !== undefined &&
          res.data.templateList[r].templateContent !== ""
        ) {
          res.data.templateList[r].templateContent = eval(
            res.data.templateList[r].templateContent
          );
          res.data.templateList[r].templateImgUrl = eval(
            res.data.templateList[r].templateImgUrl
          );
        }
        if (
          res.data.templateList[r].templateName === "checkbox" ||
          res.data.templateList[r].templateName === "piccheckbox"
        ) {
          res.data.templateList[r].data = [];
        } else {
          res.data.templateList[r].data = "";
        }
      }
      this.form = res.data;
    });
  }
};
</script>

<style scoped>
.fd-head {
  margin-top: 5px;
  margin-bottom: 5px;
}
.fd-all >>> .form-table {
  text-align: left;
}
.fd-all >>> .t-desc {
  color: gray;
  font-size: 12px;
  padding-bottom: 5px;
}
.fd-all >>> .t-radio,
.t-select,
.t-checkbox,
.t-head,
.t-textbox {
  text-align: left;
  padding: 15px 30px;
}
.selected {
  background: #f7f7f7;
  border: 1px dashed #b3b3b3;
}
.fd-tr > td {
  width: 100px;
  height: 40px;
  border-width: 0px;
  border-radius: 5px;
  cursor: pointer;
  /* 鼠标移入按钮范围时出现手势 */
  outline: none;
  /*不显示轮廓线 */
  color: #333333;
  /* 字体颜色 */
  font-size: 17px;
  /* 字体大小 */
}

.fd-tr > td:hover {
  /* 鼠标移入按钮范围时改变颜色 */
  background: #ffffff;
}

/* element 表头颜色 */
.el-table th {
  background: #fdead9;
}

/* element 表身颜色  奇数*/
.el-table tr {
  background: #fbf6f3;
}

/* element 表身颜色  偶数*/
.el-table--striped .el-table__body tr.el-table__row--striped td {
  background: #fdeadc;
}
</style>
